.faq {
  padding: 120px 0;
  background-color: $color-background-grey;

  @include vp-1365 {
    padding: 100px 0;
  }

  @include vp-767 {
    padding: 71px 0 69px;
  }
}

.faq__inner-wrapper {
  display: flex;
  gap: 40px;

  @include vp-1365 {
    flex-direction: column;
    gap: 39px;
  }

  @include vp-767 {
    gap: 29px;
  }
}

.faq__menu-buttons {
  display: flex;
  flex-direction: column;
  gap: 10px;
  justify-content: flex-start;

  @include vp-1365 {
    flex-direction: row;
    justify-content: center;
  }

  @include vp-767 {
    margin: 0 auto;
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 8px;
    row-gap: 7px;
  }
}

.faq__main-title {
  margin-bottom: 51px;
  font-family: "Alumni Sans", Arial, sans-serif;
  font-size: 54px;
  font-weight: 600;
  line-height: 54px;
  letter-spacing: 1.62px;

  @include vp-1365 {
    margin-bottom: 50px;
    margin-left: 1px;
    font-size: 42px;
    line-height: 42px;
    letter-spacing: 1.26px;
    text-align: center;
  }

  @include vp-767 {
    margin-bottom: 36px;
    margin-left: 0;
    font-size: 32px;
    line-height: 32px;
    letter-spacing: 0.96px;
  }
}

.faq__menu-button {
  width: 260px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 19px 48px 15px 48px;
  font-family: inherit;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 18px;
  color: $color-default-blue;
  background-color: $color-transparent;
  border: 1px solid $sapphire;

  @include vp-1365 {
    width: 162px;
  }

  @include vp-767 {
    width: 141px;
    padding: 15px 48px 12px;
    font-size: 16px;
    line-height: 16px;
  }

  @include hover-focus {
    border: 1px solid $color-default-blue;
  }

  @include active {
    color: $color-default-white;
    background: $red-to-blue;
    border-image: $red-to-blue;
    border-image-slice: 1;
  }

  &:disabled {
    color: $link-water;
    border: 1px solid $sapphire-2;
  }
}

.faq__menu-button.is-active {
  color: $color-default-white;
  background: $red-to-blue;
  border-image: $red-to-blue;
  border-image-slice: 1;
}

.faq__title {
  margin-bottom: 12px;
  font-weight: 400;
  font-size: 24px;
  line-height: 24px;

  @include vp-1365 {
    max-width: 555px;
    margin-bottom: 10px;
    font-size: 22px;
    line-height: 22px;
  }

  @include vp-767 {
    margin-bottom: 19px;
    padding-right: 40px;
    font-size: 20px;
    line-height: 20px;
  }
}

.faq__description {
  max-width: 660px;
  font-size: 18px;
  font-weight: 300;
  line-height: 24px;

  @include vp-1365 {
    max-width: 555px;
  }

  @include vp-767 {
    margin-bottom: 21px;
    font-size: 16px;
    line-height: 20px;
  }
}

.faq__tabs-element {
  flex-grow: 1;
}

.faq__item-wrapper {
  display: none;
}

.faq__items {
  position: relative;

  &::before {
    position: absolute;
    content: "";
    height: 1px;
    width: 100%;
    top: 0;
    left: 0;
    background-color: $resolution-blue;
  }
}

.faq__item-wrapper {
  min-width: 100%;
}

.faq__item-wrapper.is-active {
  display: block;
}

.faq__item-inner-wrapper {
  position: relative;
  padding: 34px 50px 21px 0;
}

.faq__item.is-active .faq__item-inner-wrapper {
  padding: 33px 50px 24px 0;


  @include vp-1365 {
    padding: 36px 50px 25px 0;
  }

  @include vp-767 {
    padding: 23px 16px 2px 0;
  }
}

.faq__item {
  position: relative;
  cursor: pointer;

  &::after {
    position: absolute;
    content: "";
    height: 1px;
    width: 100%;
    bottom: 0;
    left: 0;
    background-color: $resolution-blue;
  }

  &.is-active {
    .faq__decorative-button--open {
      display: block;

      @include vp-1365 {
        top: 16%;
      }

      @include vp-767 {
        top: 12%;
      }
    }

    .faq__decorative-button:not(.faq__decorative-button--open) {
      display: none;
    }
  }

  &--hours .faq__item-inner-wrapper {
    padding: 34px 50px 20px 0;

    @include vp-1365 {
      padding: 34px 50px 24px 0;
    }

    @include vp-767 {
      padding: 22px 50px 2px 0;
    }
  }

  &--qual .faq__item-inner-wrapper {
    padding: 33px 50px 21px 0;

    @include vp-1365 {
      padding: 25px 50px 15px 0;
    }

    @include vp-767 {
      padding: 22px 19px 3px 0;
    }
  }

  &--towels .faq__item-inner-wrapper {
    @include vp-1365 {
      padding: 35px 50px 23px 0;
    }

    @include vp-767 {
      padding: 23px 50px 3px 0;
    }
  }

  .faq__decorative-button {
    position: absolute;
    top: 30%;
    right: 0;
    width: 40px;
    height: 40px;
    padding: 6px;
    border: 1px solid $color-default-blue;
    border-radius: 50%;
    stroke: $color-default-blue;
    background-color: $color-transparent;
    cursor: pointer;

    @include vp-1365 {
      top: 28%;
    }

    @include vp-767 {
      width: 30px;
      height: 30px;
      top: 35%;
    }

    &--hours {
      @include vp-767 {
        top: 32%;
      }
    }

    &--open {
      display: none;
      top: 15%;
      background-color: $color-default-blue;
    }

    @include hover-focus {
      stroke: $color-default-white;
      background-color: $color-default-blue;
    }

    &--disabled {
      opacity: 0.3;
      pointer-events: none;
    }
  }
}
